'use client';
import { useState, useEffect, useRef } from 'react';

function Verify() {
  const [code, setCode] = useState<string[]>(['', '', '', '']);
  const [timer, setTimer] = useState<number>(30);
  const inputsRef = useRef<Array<HTMLInputElement | null>>([]);

  useEffect(() => {
    if (timer > 0) {
      const countdown = setTimeout(() => setTimer(timer - 1), 1000);
      return () => clearTimeout(countdown);
    }
  }, [timer]);

  const handleChange = (index: number, value: string) => {
    if (!/^\d?$/.test(value)) return;
    const newCode = [...code];
    newCode[index] = value;
    setCode(newCode);
    if (value && index < 3) {
      inputsRef.current[index + 1]?.focus();
    }
  };

  const handleResend = () => {
    if (timer === 0) {
      setCode(['', '', '', '']);
      setTimer(30);
      // TODO: Call resend API here
    }
  };

  const handleSubmit = () => {
    const finalCode = code.join('');
    if (finalCode.length === 4) {
      console.log('Verifying code:', finalCode);
      // TODO: Call verify API here
    }
  };

  return (
    <form>
      <h2>Verify Email</h2>
      <p>We've sent a verification code to your email. Please enter it below.</p>
      <div>
        {code.map((digit, idx) => (
          <input
            key={idx}
            ref={el => (inputsRef.current[idx] = el)}
            type="text"
            maxLength={1}
            value={digit}
            onChange={(e) => handleChange(idx, e.target.value)}
          />
        ))}
      </div>
      <p>
        <button type="button" onClick={handleResend} disabled={timer > 0}>
          Resend code
        </button>{' '}
        in {`0:${timer.toString().padStart(2, '0')}`}
      </p>
      <button type="button" onClick={handleSubmit} disabled={code.some(c => c === '')}>
        Continue
      </button>
    </form>
  );
}

export default Verify;
